

.FrontNavBar {
    position: sticky;
    top: 1vh;
    height: 10vh;
}

.FrontProfile {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FrontAbout {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FrontProjects {
    height: 100vh;
    display: flex;
}

.FrontFooter {
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.FrontCv {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}


.NavBar {
    display: flex;
    justify-content: space-between;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
}

.NavButtons{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.name {
    display: flex;
    font-size: 3vh;
    margin: 2vh;
    align-items: start;
}

.ProfileButton {
    width: 11vh;
    height: 5vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}

.ProjectButton {
    width: 11vh;
    height: 5vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}

.CvButton {
    width: 11vh;
    height: 5vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}